.swiper{
    width: 100%;
    height: 100%;
    margin-left: auto;
    margin-right: auto;
}
.swiper-slide{
    text-align: center;
    font-size: 18px;
    background: #fff;
    height: calc((100% - 30px) / 2) !important;
}
.swiper-slide .design-img{
    display: block;
    width: 445px;
    height: 445px;
    object-fit: cover;
}
